:root {
  --mainColour: #2093d5;
  --inset1:  #1b7db5;
  --inset1:  ash;
  --mainColour: rgb(159, 166, 167);
  --mainColourDark: rgb(44, 42, 42);
  --insetDark: black;

  --screenColor: rgb(92, 112, 92);
  --buttonPress: grey;
}
@font-face {
  font-family: myFirstFont;
  src: url(./DIGITALDREAMFATSKEW.ttf)
}


body {
  margin: 0;
  background-color: var(--mainColour);
  display: flex;
  justify-content: center;
  align-content: center;
}


.App {
  background-color: var(--mainColour);
  margin: auto;
  width: 100vmin;
  max-width: 800px;
  height: 100vh;
  
  /* padding: 10px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: 0.5s;
}


.screenInput {
  /* height: 20vh; */
  font-size: 20px;
  resize: none;
  width: 100%; 
  max-width: 600px;
  font-family: myFirstFont;
  font-weight: 600;
  line-height: 1.5;
  height: 25vh;
  background-color: var(--screenColor);
  border-radius: 5px;
  margin-bottom: 3vh;
  margin-top: 1vh;
  padding: 2vw;
  border: 2px inset black;
  box-sizing: border-box;
  outline: none;
box-shadow: 1px 0px 2px 1px rgba(0,0,0,0.75) inset;
-webkit-box-shadow: 1px 0px 2px 1px rgba(0,0,0,0.75) inset;
-moz-box-shadow: 1px 0px 2px 1px rgba(0,0,0,0.75) inset;
}


.screens {
  width: 92vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}
.text {
  width: 92vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: none;
  outline: none;
}


.time {
  padding: 5px;
  display: flex;
  font-family: myFirstFont;
  resize: none;
  width: 40%;
  max-width: 300px;
  height: 1rem;
  max-height: 2rem;
  font-size: 20px;
  font-weight: 500;
  background-color: var(--screenColor);
  border: 2px solid black;
  border-radius: 5px;
  outline: none;
  box-shadow: 1px 0px 2px 1px rgba(0,0,0,0.75) inset;
-webkit-box-shadow: 1px 0px 2px 1px rgba(0,0,0,0.75) inset;
-moz-box-shadow: 1px 0px 2px 1px rgba(0,0,0,0.75) inset;
}
.themeIcon {
  height: 100%;
}
.output {
  height: 30%;
  background-color: red;
  width: 100%;
}

.Buttons {
  display: grid;
  grid-template-columns: 23% 23% 23% 23%;
  gap: 2%;
  justify-content: center; 
  /* align-content: center;  */
  height: 60%;
  width: 92vw;
  max-width: 100%;
}
.buttonContainer {
  background-color: var(--inset1);
  width: 100%;
  max-width: 800px;
  height: 100%;
  border-radius: 20%;
  box-shadow: inset 5px 5px 10px var(--inset1)
            inset -5px -5px 10px #25a9f5;
    
 display: flex;
 justify-content: center;
 align-items: center;    
}

.buttonContainer button {
  transition: 0.5s;
  font-size: 3.2vh;
  font-weight: 700;
  height: 90%;
  width: 90%;
  /* margin: 0vh; */
  border: none;
  border-radius: 20%;
  background-color: var(--mainColour);
  box-shadow:  1px 1px 2px #0a1920,
               -1px -1px 2px #112431;
      
  }
  .buttonContainer button:active {
    background-color: var(--buttonPress);
  }
  .topButtons {
    display: flex;
    width:  90%;
    align-items: center;
    gap: 2vw;
    height: 10vh;
  }
  .smallButtons {
    height: 3vw;
    width: 3vw;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
            
    background-color: #464649;
    box-shadow: inset 7px 7px 14px #303032,
                inset -7px -7px 14px #5c5c60; 
  }
  .output, .input {
    font-size: 4vh;
    display: flex;
    align-items: center;
    justify-content: center;
    resize: none;
    font-family:'Courier New', Courier, monospace;
    font-weight: 1000;
    border: none;
    border-radius: 2vh;
    background: #1A1A1A;
    box-shadow: inset 3px 3px 5px rgba(0, 0, 0, 0.25),
     inset -3px -3px 5px #202020;
     background: var(--mainColour);
  

}
.smallButtons button {
  font-size: 3.2vh;
  font-weight: 700;
  border: none;
  border-radius: 100%;
  color:white;
  background-color: black;
  /* background: #202122;
  box-shadow: inset 9px 9px 18px #1b1c1d,
            inset -9px -9px 18px #252627; */
background-color: linear-gradient(137.81deg, #1D222E 21.35%, #293140 106.56%);
mix-blend-mode: normal;
border: 3px solid rgba(41, 49, 64, 0.65);
box-sizing: border-box;
box-shadow: -5px -5px 10px rgba(72, 80, 99, 0.1), 5px 5px 10px rgba
(24, 28, 39, 0.35), inset -2px -2px 4px rgba(72, 80, 99, 0.35), inset 5px 5px 5px #1D222E
}
.theme {
  /* background-color: red; */
  width: 90%;
  height: 90%;
  background-color: black;
  background-image: url("./images/ClockCounterClockwise.png");
  background-size: cover;
}
.coloured {
  color: red !important;
}

